html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.875;
}

.noscroll {
  overflow: hidden;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (max-width: 320px) {
  .container {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .container {
    width: 768px;
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

picture {
  margin: 0;
  padding: 0;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-family: inherit;
}

:root {
  --focus-pinc-color: #d41443;
  --accent-salmon-color: #ffa5ba;
  --text-black-color: black;
  --text-gray-color: #907e82;
  --focus-white-color: #fff;
  --main-transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --bcg-pink-color: #ffb8ca;
  --pink-btn-hover: #c7133e;
  --white-btn-hover: #ecebeb;
}

.header {
  background-color: #ffb8ca;
}

.header-container {
  align-items: center;
  padding-top: 37px;
  display: flex;
}

.site-nav {
  display: flex;
}

.header-nav {
  align-items: center;
  margin-left: 175px;
  display: flex;
}

.header-nav__item {
  color: var(--focus-white-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  text-decoration: none;
}

.header-nav__item:not(:last-child) {
  margin-right: 35px;
}

.header-nav__link {
  color: var(--focus-white-color);
  transition: var(--main-transition);
  z-index: 100;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  text-decoration: none;
}

.header-nav__link:hover, .header-nav__link:focus {
  color: var(--focus-pinc-color);
}

.header-button {
  width: 159px;
  height: 44px;
  background: var(--focus-white-color);
  color: var(--focus-pinc-color);
  cursor: pointer;
  transition: var(--main-transition);
  border: 0;
  border-radius: 22px;
  align-items: center;
  margin-left: 174px;
  padding-left: 37px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

.header-button:hover, .header-button:focus {
  background: #ecebeb;
}

.header-button__icon {
  fill: currentColor;
  margin-left: 12px;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 10;
  opacity: 1;
  transition: opacity var(--main-transition), visibility var(--main-transition);
  background-color: rgba(0, 0, 0, .3);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.header-modal {
  width: 93vw;
  height: 100%;
  max-width: 450px;
  max-height: 600px;
  background-color: var(--focus-white-color);
  transition: transform var(--main-transition);
  border-radius: 30px;
  padding: 50px 15px 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.backdrop.is-hidden .header-modal {
  transform: translate(-50%, -50%)scale(1.3);
}

@media screen and (min-width: 1280px) {
  .header-modal {
    max-height: 640px;
    padding: 50px 20px 30px;
    overflow-y: visible;
  }
}

.modal-header-close-btn {
  width: 15px;
  height: 15px;
  background: inherit;
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
}

.modal-header-close-btn:after, .modal-header-close-btn:before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--accent-salmon-color);
  transition: background-color var(--main-transition);
  position: absolute;
  top: 50%;
  left: 0;
}

.modal-header-close-btn:before {
  transform: rotate(45deg);
}

.modal-header-close-btn:after {
  transform: rotate(-45deg);
}

.modal-header-close-btn:hover:after, .modal-header-close-btn:hover:before, .modal-header-close-btn:focus:after, .modal-header-close-btn:focus:before {
  background-color: var(--pink-btn-hover);
}

.modal-title {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--pink-btn-hover);
  text-align: center;
  margin-bottom: 12px;
  font-family: Titan One, sans-serif;
  font-size: 24px;
  line-height: 1.12;
}

@media screen and (min-width: 768px) {
