.checkbox {
  margin-top: 15px;
  margin-bottom: 30px;
  padding: 0;
}

.checkbox-label {
  justify-content: center;
  align-items: center;
  font-size: 12px;
  line-height: 1.17;
  display: flex;
}

.checkbox-box {
  width: 16px;
  height: 14px;
  border: 1px solid var(--accent-salmon-color);
  transition: background-color var(--main-transition), border var(--main-transition), box-shadow var(--main-transition);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
  display: flex;
  box-shadow: 0 1px 4px rgba(199, 19, 62, .2);
}

